@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

.display {
  width: 125px;
  height: 50px;
  border: 5px;
  border-style: solid;
  border-color: #848a88;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-bottom: 10px;
  font-size: 24px;
  background-color: #dcdedd;
  font-family: 'Orbitron', sans-serif;
  color: #4f635d;
}

.round {
  font-size: 10px;
  font-weight: bold;
  background-color: #dcdedd;
  font-family: 'Orbitron', sans-serif;
  color: #4f635d;
  position: relative;
  top: -170px;
  right: -140px;
  width: 50px;
}

.runningTimer {
  background-color: #b8dcb0; /* Green background for running timer */
}

.notRunningTimer {
  background-color: #ececec; /* Red background for not running timer */
}
